:root {
  --black-bg-color: #202025;
  --white-bg-color: #fcfcfc;
}

body {
  font-style: normal;
}

html {
  box-sizing: border-box;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

